<template>
  <div id="wolder">
    <div class="header1">
      <div class="svg1">
        <img src="https://gw.alicdn.com/tps/TB1W_X6OXXXXXcZXVXXXXXXXXXX-400-400.png" alt />
      </div>
      <router-link v-for="x in router" :to="x.path" tag="li">{{x.title}}</router-link>
    </div>
    <div class="content">
      <router-view />
    </div>
    <div class="footer"></div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      router: [
        { title: "首页", path: "/home"},
        { title: "留言", path: "/say" },
        { title: "相册", path: "/phone" }
      ]
    };
  }
};
</script>

<style lang="scss" scoped>
#wolder {
  display: flex;
  flex-direction: column;
//   justify-content: space-between;
  widows: 100%;
  height: 100%;
  .header1 {
    height: 10%;
    color: #090723;
    width: 100%;
    display: flex;
    justify-content: flex-end;
    background-color: #142334;
    // background: url(../../assets/time.png) no-repeat;
    // background-position: center;
    // background-size:100% 100%;
      position: relative;
    li {
      display: flex;
      justify-content: flex-end;
      align-items: center;
      padding-right: 7%;
      list-style: none;
      color: rgba(255, 255, 255, 0.5);
    }
    .svg1 {
      position: absolute;
      height: 100%;
      left: 2%;
      img {
        height: 100%;
      }
    }
    li:hover {
      color: whitesmoke;
    }
  }
  .content {
    // -webkit-box-flex: 1;
  }
  .footer {
    height: 20%;
    width: 100%;
  }
}
</style>


