import Vue from 'vue'
import Router from 'vue-router'
// import index from '@/components/index/index'
import futrue from '@/components/futrue/index'
import left from '@/components/left/index'
import word from '@/components/word/index'

Vue.use(Router)

export default new Router({
  module:history,
  routes: [
    {
      path: '/',
      name:'futrue',
      component:futrue
    },
    {
      path: '/home',
      name: 'futrue',
      component: futrue
    },{
      path: '/say',
      name: 'left',
      component: left
    },{
      path: '/phone',
      name: 'word',
      component: word
    }
  ]
})



